import React from 'react';
import styled, { keyframes } from 'styled-components';

import { _color } from '../base/constants';

/**
 * Styled Components
 */
const spin = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
`;

const PreloaderBlock = styled.div`
  display: flex;
  justify-content: center;
`;

const PreloaderSpinner = styled.div`
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1px solid transparent;
  border-top-color: ${_color.secondary};
  border-bottom-color: ${_color.secondary};
  animation: ${spin} 2s linear infinite;

  &:before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border-radius: 50%;
    border: 1px solid transparent;
    border-top-color: ${_color.dark};
    /* border-bottom-color: ${_color.dark}; */
    border-left-color: ${_color.dark};
    animation: ${spin} 3s linear infinite;
  }

  &:after {
    content: '';
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    border-radius: 50%;
    border: 1px solid transparent;
    border-top-color: ${_color.accent};
    border-bottom-color: ${_color.accent};
    border-left-color: ${_color.accent};
    animation: ${spin} 1.5s linear infinite;
  }
`;

/**
 * Preloader Component
 */
const Preloader = () => {
  return (
    <PreloaderBlock>
      <PreloaderSpinner />
    </PreloaderBlock>
  );
};

export default Preloader;
