import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import { rgba } from 'polished';

import { _color } from '../base/constants';

import firebase from 'firebase/app';
import 'firebase/firestore';

import Preloader from '../components/Preloader';

const config = {
  apiKey: 'AIzaSyDoRq2cYDFEu0sH_ig3ojGn4vTZiZ0u44c',
  authDomain: 'alexnesterov-3c0fe.firebaseapp.com',
  databaseURL: 'https://alexnesterov-3c0fe.firebaseio.com',
  projectId: 'alexnesterov-3c0fe',
  storageBucket: 'alexnesterov-3c0fe.appspot.com',
  messagingSenderId: '561786538798',
};

firebase.initializeApp(config);

/**
 * Styled Components
 */
const showImage = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

const WorksBlock = styled.div``;

const WorksList = styled.ul`
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: -40px 0 0;
  padding: 0;
  list-style: none;
`;

const WorksItem = styled.li`
  flex: 1 1 365px;
  max-width: 365px;
  margin: 60px 0 0;
  padding: 0 30px 0 15px;
  text-shadow: 0 4px 4px ${rgba(_color.dark, 0.2)};
`;

const WorksLink = styled.a`
  position: relative;
  display: block;
  color: inherit;

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 126px;
    z-index: 1;
    border: 3px solid ${_color.dark};

    ${WorksItem}:hover & {
      border-color: ${_color.accent};
    }
  }
`;

const WorksHead = styled.div`
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0 -60px;
  padding: 10px 15px 65px 30px;
  transition: 0.2s ease-in-out;

  ${WorksItem}:hover & {
    color: ${_color.light};
    transform: translate(15px, 30px);
  }
`;

const WorksTitle = styled.h3`
  margin: 0;
  color: inherit;
  font-size: 1.125rem;
  line-height: 1.333333333;
  font-weight: 500;
  text-transform: uppercase;

  &:before {
    content: '/';
  }

  ${WorksItem}:hover & {
    background: ${rgba(_color.dark, 0.4)};
  }
`;

const WorksDesc = styled.div`
  font-size: 1rem;

  ${WorksItem}:hover & {
    background: ${rgba(_color.dark, 0.4)};
  }
`;

const WorksPreview = styled.div`
  position: relative;
  left: 30px;
  width: 320px;
  height: 480px;
  background: ${rgba(_color.accent, 0.2)};
  transition: 0.2s ease-in-out;

  ${WorksItem}:hover & {
    z-index: 1;
    box-shadow: 0 0 20px ${rgba(_color.dark, 0.1)},
      0 60px 40px -30px ${rgba(_color.dark, 0.2)};
    transform: translateY(-30px) scale(1.05);
  }
`;

const WorksImage = styled.img`
  display: block;
  animation: ${showImage} 0.2s;
`;

/**
 * Works Component
 */
class Works extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      works: [],
    };
  }

  componentDidMount() {
    const firestore = firebase.firestore();
    const settings = { timestampsInSnapshots: true };
    firestore.settings(settings);

    firestore
      .collection('works')
      .get()
      .then(querySnapshot => {
        this.setState({
          works: querySnapshot.docs,
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <WorksBlock>
        {this.state.isLoading ? (
          <Preloader />
        ) : (
          <WorksList>
            {this.state.works.map(work => (
              <WorksItem key={work.id}>
                <WorksLink href={work.data().link} target="_blank">
                  <WorksHead>
                    <WorksTitle>{work.data().title}</WorksTitle>
                    <WorksDesc>{work.data().desc}</WorksDesc>
                  </WorksHead>
                  <WorksPreview>
                    <WorksImage src={work.data().image} alt="" />
                  </WorksPreview>
                </WorksLink>
              </WorksItem>
            ))}
          </WorksList>
        )}
      </WorksBlock>
    );
  }
}

export default Works;
